.container-fluid {
  min-height: 100vh;
  overflow-y: hidden;
}
.buttons {
  width: 100px;
  height: 40px;
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 5px;
  display: inline-block;
}
.check {
  width: 1000px;
  height: 40px;
  position: absolute;
  top: 0;
  padding-left: 10px;
  padding-top: 13px;
  display: inline-block;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
